*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global Styles */
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
}

a, button, input, textarea {
  min-width: 44px;
  min-height: 44px;
}

/* Skip to Content */
.skip-to-content {
  position: absolute;
  top: -40px;
  left: 0;
  background: #000;
  color: #fff;
  padding: 8px;
  z-index: 1000;
}

.skip-to-content:focus {
  top: 0;
}

/* App Bar */
.app-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #333;
  color: #fff;
  padding: 0.5rem;
}

.app-bar__brand {
  font-size: 1.5rem;
  font-weight: bold;
}

.app-bar__menu {
  display: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.app-bar__nav {
  display: flex;
}

.app-bar__nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.app-bar__nav li {
  margin: 0 0.5rem;
}

.app-bar__nav a {
  color: #fff;
  text-decoration: none;
  padding: 12px;
  min-width: 44px;
  min-height: 44px;
  display: inline-block;
  text-align: center;
}

/* Hero Section */
.hero {
  position: relative;
  width: 100%;
  height: 50vh;
  overflow: hidden;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.hero__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  padding: 1rem;
}

/* Restaurant List */
.restaurant-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  padding: 1rem;
}

.restaurant-item {
  max-width: 300px;
  width: 100%;
  background: #f4f4f4;
  border-radius: 8px;
  overflow: hidden;
  text-align: center;
}

.restaurant-item img {
  width: 100%;
  height: auto;
}

/* Footer */
footer {
  background: #333;
  color: #fff;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
}

/* Responsive Layouts */

/* Small Screens (max-width: 768px) */
@media (max-width: 768px) {
  .app-bar__nav {
    display: none;
    flex-direction: column;
    background: #333;
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    z-index: 1000;
  }

  .app-bar__menu {
    display: block;
  }

  .app-bar__nav.open {
    display: flex; /* Menampilkan menu ketika tombol hamburger diklik */
  }

  .app-bar__nav a {
    padding: 12px; 
    min-width: 44px; 
    min-height: 44px; 
    display: inline-block; 
    text-align: center; 
  }
}
/* Medium Screens (min-width: 768px) */
@media (min-width: 768px) {
  .hero-image {
    height: 50vh;
  }
}

/* Large Screens (min-width: 1200px) */
@media (min-width: 1200px) {
  .hero-image {
    height: 50vh;
    object-fit: cover;
  }
}
/* Detail Page Styles */
.restaurant-detail {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 2rem;
  background: linear-gradient(to bottom, #f7f8fa, #eaeff5);
  min-height: 100vh;
}

/* Header Section */
.restaurant-header {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: center;
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.restaurant-header:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

.restaurant-detail img {
  width: 100%;
  height: auto;
  border-radius: 16px;
  flex-shrink: 0;
}

.restaurant-header-content {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  flex: 1;
}

.restaurant-header h2 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #2d4059;
  margin-bottom: 0.5rem;
  border-bottom: 3px solid #ff7b00;
  display: inline-block;
}

.restaurant-header p {
  font-size: 1.1rem;
  color: #596275;
  margin: 0.3rem 0;
}

/* Description Section */
.restaurant-description {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #2d4059;
  background: #ffffff;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.restaurant-description:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
}

/* Menus Section */
.restaurant-menus {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: space-between;
}

.menu-section {
  flex: 1;
  min-width: 280px;
  background: linear-gradient(to bottom, #ffffff, #f9f9f9);
  padding: 1.5rem;
  border-radius: 16px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.menu-section:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12);
}

.menu-section h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #ff7b00;
  border-bottom: 3px solid #ffd080;
  display: inline-block;
  padding-bottom: 0.3rem;
}

.menu-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-section ul li {
  font-size: 1.1rem;
  color: #4b6584;
  margin-bottom: 0.8rem;
}

/* Reviews Section */
.customer-reviews {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.customer-reviews h3 {
  font-size: 1.8rem;
  color: #2d4059;
  border-bottom: 3px solid #ff7b00;
  padding-bottom: 0.5rem;
  display: inline-block;
}

.review-item {
  background: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  padding: 1.5rem;
  border: 1px solid #ddd;
  border-radius: 16px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.review-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.review-item p {
  font-size: 1rem;
  color: #596275;
}

/* Favorite Button */
.favorite-button {
  align-self: center;
  padding: 0.8rem 2rem;
  font-size: 1.1rem;
  color: #ffffff;
  background: linear-gradient(to right, #ff7b00, #ff3d00);
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  box-shadow: 0 4px 10px rgba(255, 125, 0, 0.3);
}

.favorite-button:hover {
  background: linear-gradient(to right, #ff9a3d, #ff6447);
  transform: scale(1.05);
  box-shadow: 0 6px 14px rgba(255, 100, 0, 0.4);
}

.favorite-button.active {
  background: linear-gradient(to right, #ff5c5c, #e60000);
}

.favorite-button.active:hover {
  background: linear-gradient(to right, #ff7f7f, #ff3333);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .restaurant-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .menu-section {
    flex: 1 1 100%;
  }

  .favorite-button {
    width: 100%;
    text-align: center;
  }
}

#installButton {
  display: none; /* Initially hidden */
  background: linear-gradient(135deg, #ff7b00, #ff3d00); /* Gradient background */
  color: #fff;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 12px 24px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  text-align: center;
  width: 200px;
  margin: 20px auto;
}

#installButton:hover {
  background: linear-gradient(135deg, #ff9a3d, #ff6447); /* Lighter gradient on hover */
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

#installButton:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 125, 0, 0.3); 
}

#installButton:active {
  transform: scale(0.98); 
}

a.cta {
  display: inline-block; 
  background-color: #ff9a3d;
  padding: 6px 1px;
  border-radius: 5px;
  min-width: 44px; 
  min-height: 44px; 
  line-height: 44px; 
  text-align: center; 
  padding: 5px; 
  text-decoration: none; 
  color: #fff; 
}
